import { useState, useEffect } from "react";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import "./EditForm.css";

export default function EditForm({ post, onSave, onClose }) {
  const [title, setTitle] = useState(post.title);
  const [description, setDescription] = useState(post.description);
  const [tags, setTags] = useState(post.tags.join(", "));
  const [folders, setFolders] = useState([]);
  const [selectedFolder, setSelectedFolder] = useState(post.folderId || "");

  const apiBase = process.env.NEXT_PUBLIC_API_URL;
  useEffect(() => {
    const userId = localStorage.getItem("userId");
    fetch(`${apiBase}/api/folders/user/${userId}`)
      .then(res => res.json())
      .then(data => setFolders(data.data || []));
  }, [apiBase]);

  async function handleEdit(e) {
    e.preventDefault();
    const tagsArray = tags.split(",").map(tag => tag.trim()).filter(Boolean);
    await fetch(`${apiBase}/api/posts/${post._id}`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        title,
        description,
        tags: tagsArray,
        folderId: selectedFolder || null,
      }),
    });
    if (onSave) onSave();
    if (onClose) onClose();
  }

  return (
    <form onSubmit={handleEdit} className="edit-form">
      <img
        src={post.imageUrl}
        alt={post.title}
        className="edit-form-img"
      />
      <TextField
        label="Title"
        value={title}
        onChange={e => setTitle(e.target.value)}
        required
        variant="outlined"
        fullWidth
      />
      <TextField
        label="Description"
        value={description}
        onChange={e => setDescription(e.target.value)}
        multiline
        minRows={2}
        variant="outlined"
        fullWidth
      />
      <TextField
        label="Tags (comma separated)"
        value={tags}
        onChange={e => setTags(e.target.value)}
        variant="outlined"
        fullWidth
      />
  <FormControl fullWidth className="edit-form-folder-select">
        <InputLabel id="folder-select-label">Folder</InputLabel>
        <Select
          labelId="folder-select-label"
          value={selectedFolder}
          label="Folder"
          onChange={e => setSelectedFolder(e.target.value)}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {folders.map(folder => (
            <MenuItem key={folder._id} value={folder._id}>
              {folder.name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <Button type="submit" variant="contained" color="primary">
        Save Changes
      </Button>
    </form>
  );
}